.Typing {
  width: 80%;
  height: 100vh;
  margin: auto;
}

.Typing h1 {
  font-family: 'Lobster', cursive;
  margin-top: 0;
  padding-top: 10vh;
  color: rgba(0,212,255,1);
  font-size: 4rem;
}

.Typing p {
  font-family: 'Roboto Mono', monospace;
  margin-top: 0;
  padding-top: 3vh;
  color: rgba(0,212,255,1);
  font-size: 1.6rem;
  margin: auto;
}

.Typing .blinkingCursor {
  font-weight: 900;
  font-size: 4rem;
  color: #ddd;
  -webkit-animation: 750ms blink step-end infinite;
  -moz-animation: 750ms blink step-end infinite;
  -ms-animation: 750ms blink step-end infinite;
  -o-animation: 750ms blink step-end infinite;
  animation: 750ms blink step-end infinite;
}

@keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #ddd;
  }
}

@-moz-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color:#ddd;
  }
}

@-webkit-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #ddd;
  }
}

@-ms-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #ddd;
  }
}

@-o-keyframes blink {
  from, to {
    color: transparent;
  }
  50% {
    color: #ddd;
  }
}

.DisplayedText {
  font-family: 'Roboto Mono', monospace;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  font-size: 2.3rem;
  color: #ddd;
}

.InputField {
  margin: 2rem;
}

.InputField input {
    padding: 2rem 4rem;
    font-size: 2.3rem;
}

.ButtonContainer {
  margin-top: 3rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.ButtonContainer button {
  font-family: 'Lobster', cursive;
  padding:0.5rem 2rem;
  border:0.2rem solid #FFFFFF;
  margin:0 0.6rem 0.6rem 0;
  border-radius:0.5rem;
  box-sizing: border-box;
  text-decoration:none;
  font-weight:300;
  color:#000;
  text-align:center;
  transition: all 0.2s;
  font-size: 1.8rem;
}

.ButtonContainer button:hover {
  background-color: rgba(0,212,255,1);
  border:0.2rem solid rgba(0,212,255,1);
}

.ButtonContainer h3 {
  color: #ddd;
  font-size: 1.6rem;
}

@media only screen and (max-width: 1100px) {
  .Typing {
    width: 90%;
  }
  .Typing h1 {
    font-size: 2rem;
  }
  .Typing p {
    font-size: 1.3rem
  }
  .DisplayedText {
    font-size: 1.3rem
  }
  .InputField input{
    width: 60%;
    margin-bottom: 3rem;
    margin-top: 3rem;
  }
  .ButtonContainer button {
    padding:0.25rem 1rem;
    margin:0 0.3rem 0.3rem 0;
    border-radius:0.25rem;
    font-size: 1.3rem;
  }
}

